import uniPopup from '@/components/uni-popup/uni-popup.vue'
export default {
	components: {uniPopup},
	data() {
		return {
			limit:10,
			page:1,
			leftTitle:'庇佑大家心想事成',
			rightTitle:'西路财神纳珍天尊',
			imgUrl:'',
			status:0,//0需要氪金了 1氪金cd中
			tmpImg:'',
			width:452,
			height:650,
			pixelRatio:2,
			bgPath:'/static/tmp/pbg.png',//底板
			codePath:'/static/tmp/code.jpg',//小程序码
			// god:'/static/tmp/x2.png',
			shareInfo:{
				title:'',
				poser:''
			},
			packageList:[],
			detail:{},//道场详情
		}
	},
	computed:{
		bgImage(){
			return `url(${this.imgUrl})`
		}
	},
	onLoad(params) {
		wx.getSystemInfo({
		  success:(res)=>{
			// 通过像素比计算出画布的实际大小（330x490）是展示的出来的大小
			this.width = 226 * res.pixelRatio
			this.height = 325 * res.pixelRatio
			this.pixelRatio=res.pixelRatio
		  }
		})
		this.getShareInfo()
		this.getPackage()
		try{
			let detail=JSON.parse(params.detail)
			detail.content=decodeURIComponent(detail.content)
			this.detail=detail
			this.rightTitle=this.detail.info.substr(0,8)
			this.imgUrl=this.detail.main_image
		}catch(e){
			//TODO handle the exception
		}
		uni.getStorage({
		    key: 'name',
		    success: (res)=> {
		        this.leftTitle='庇佑'+res.data+'心想事成'
		    }
		});
		console.log(params)
		if(params.status){
			this.status=1
		}else if(params.expired){
			this.open()
			this.status=0
		}else{
			this.status=0
		}
	},
	onShow(){
		wx.hideHomeButton()
	},
	onShareAppMessage(res) {
		if (res.from === 'button') {// 来自页面内分享按钮
		  console.log(res.target)
		}
		return {
		  title:this.shareInfo.title,
		  path: '/pages/index/index'
		}
	},
	methods: {
		//上香啊
		jin(){
			let prayer=uni.getStorageSync('paryData').id
			this.$api.put('/api/prayer/'+prayer).then((res)=>{
				console.log(res)
				uni.showToast({
					title:'上香成功',
					icon:'none'
				})
			}).catch((err)=>{
				console.log(err)
				if(err.code==500){
					this.open()
					this.status=0
				}
			})
		},
		//获取套餐
		getPackage(){
			this.$api.get('/api/pray',{
				params:{
					limit:this.limit,
					page:this.page
				}
			}).then((res)=>{
				console.log(res)
				this.packageList=this.packageList.concat(res.data)
			})
		},
		//获取分享配置
		getShareInfo(){
			this.$api.get('/api/user/share').then((res)=>{
				console.log(res)
				this.shareInfo=res
			})
		},
		closePoser(){
			this.$refs.poster.close()
		},
		//生成海报
		getPoster(){
			this.$refs.share.close()
			uni.showLoading({
			    title: '海报生成中...',
			});
			let that = this
			let context = wx.createCanvasContext('myCanvas');
			context.width = this.width;
			context.height = this.height;
			let x = context.width / 2;
			wx.getImageInfo({
			  src: this.shareInfo.poser,
			  success: (res) => {
				console.log(res)
				context.drawImage(this.bgPath, 0,0,this.width,this.height);
				context.drawImage(res.path,0,0,226*this.pixelRatio,210*this.pixelRatio,);
				context.setFontSize(12*this.pixelRatio);
				context.setFillStyle('#935C41');
				context.setTextAlign('center');
				let text=this.shareInfo.title
				if(text.length>16){
					text=text.substr(0,16)+'...'
				}
				context.fillText(text,this.width/2, 226*this.pixelRatio);
				// if(this.pixelRatio===2){
				// 	context.drawImage(this.god,0,0,206*this.pixelRatio,159*this.pixelRatio,10*this.pixelRatio,51*this.pixelRatio,206*this.pixelRatio,159*this.pixelRatio);
				// }else{
				// 	context.drawImage(this.god,0,0,206*this.pixelRatio,159*this.pixelRatio,46*this.pixelRatio,51*this.pixelRatio,206*this.pixelRatio,159*this.pixelRatio);
				// }
				context.save();
				context.restore();
				wx.getImageInfo({
				  src: that.codePath,
				  success: (res1) => {
					context.drawImage(that.codePath, 13*that.pixelRatio,258*that.pixelRatio,52*that.pixelRatio,52*that.pixelRatio);
					context.save();
					context.restore();
					context.draw();
					setTimeout(()=>{
					  wx.canvasToTempFilePath({
						canvasId: 'myCanvas',
						x: 0, //指定的画布区域的左上角横坐标	
						y: 0, //指定的画布区域的左上角纵坐标	
						width: this.width, //指定的画布区域的宽度
						height: this.height, //指定的画布区域的高度
						destWidth: this.width, //输出的图片的宽度 
						destHeight: this.height, //输出的图片的高度
						success: (res)=>{
						  var tempFilePath = res.tempFilePath;
						  this.tmpImg=tempFilePath
						  console.log(tempFilePath)
						  uni.hideLoading();
						  this.$refs.poster.open()
						},
						fail: (res)=>{
						  console.log(res);
						  uni.hideLoading();
						}
					  });
					}, 300);
				  }
				})
		 
			  }
			})
		},
		saveImageToPhotosAlbum() {
			var that = this
			var value = that.tmpImg;// 你的图片路径
			if (value != undefined && value != "") {
			  wx.saveImageToPhotosAlbum({
				filePath: value,
				success: (res) => {
				  // that.hideModal();
				  // that.hideMoments();
				  wx.showToast({
					title: "已保存到相册",
					icon: 'none',
					duration: 1500,
					mask: true
				  })
				},
				fail: function (res) {
				  console.error(res)
					//首次保存会询问你是否授权，选是就好了
				  // if (res.errMsg == "saveImageToPhotosAlbum:fail auth deny") {
				  console.error("打开设置窗口");
				  wx.openSetting({
					success(settingdata) {
					  console.error(settingdata)
					  if (settingdata.authSetting["scope.writePhotosAlbum"]) {
						console.error("获取权限成功，再次点击图片保存到相册")
					  } else {
						console.error("获取权限失败")
					  }
					}
				  })
				  // }
				}
			  })
			}
		  },
		showShare(){
			this.$refs.share.open()
		},
		goBack(){
			uni.reLaunch({
			    url: '/pages/index/index'
			});
		},
		//套餐过期
		open(){
			this.$refs.popup.open()
		},
		close(){
			this.status=0
			this.$refs.popup.close()
		},
		wxPay(pray_id){
			uni.showLoading({
			    title: '加载中',
				mask:true
			});
			wx.login({
			  success:(res)=>{
				if (res.code) {
					this.$api.post('/api/wechat/pay',{
						dojo_id:this.detail.id,
						pray_id,
						prayer_id:uni.getStorageSync('paryData').id
					}).then((info)=>{
						console.log(info)
						wx.requestPayment({
								'appId': info.appId,
								'timeStamp': info.timeStamp,
								'nonceStr': info.nonceStr,
								'package': info.package,
								'signType': info.signType,
								'paySign': info.paySign,
								'success':(res2)=>{
									console.log(res2)
									console.log("支付成功")
									uni.showToast({
										title:"支付成功",
										duration:1200
									})
									this.status=1
								},
								'fail':function(err1){
									console.log("支付失败")
									uni.showToast({
										title:'支付失败',
										icon:'none',
										duration:1200
									})
								},
								'complete':function(err2){
									uni.hideLoading();
								}
						})
					})
					
				}else {
				  console.log('登录失败！' + res.errMsg)
				}
			  }
			})		
		}
	}
}
